// Mobile first
@custom-media --gte-sm (width >= env(--sm));
@custom-media --gte-md (width >= env(--md));
@custom-media --gte-lg (width >= env(--lg));
@custom-media --gte-xl (width >= env(--xl));

// Desktop first
@custom-media --lt-xl (width < env(--xl));
@custom-media --lt-lg (width < env(--lg));
@custom-media --lt-md (width < env(--md));
@custom-media --lt-sm (width < env(--sm));

// Grid
$grid-breakpoints: (
	xs: env(--xs),
	sm: env(--sm),
	md: env(--md),
	lg: env(--lg),
	xl: env(--xl)
);

$container-max-widths: (
	lg: env(--lg)
);

$grid-gutter-width: 2rem;
@function env($variable) {
	@error "\"env()\" function was not provided by PostCSS or dollar-variable was used as passed argument";
}

@function media($query...) {
	@error "\"media()\" function was not provided by PostCSS or was called without arguments";
}
@mixin clearfix {
	&::after {
		content: "";
		display: table;
		clear: both;
	}
}
%reset {
	all: initial;

	// Properties dependent of browser
	quotes: "“" "”" "‘" "’";
	font-family: var(--content-font-family);
	text-align: left;
	color: var(--content-color);
	outline-color: var(--content-color);

	&:focus-visible {
		outline: 1px dotted currentColor;
		outline: 5px auto -webkit-focus-ring-color;
	}

	// Overwrite some properties
	display: block;
	box-sizing: border-box;
}

@mixin reset {
	@extend %reset;
}
@mixin retina {
	// stylelint-disable-next-line media-feature-name-no-vendor-prefix
	@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
		@content;
	}
}
@mixin triangle($direction, $width, $height, $color) {
	$top-width: 0;
	$top-color: rgba(0, 0, 0, 0);
	$right-width: 0;
	$right-color: rgba(0, 0, 0, 0);
	$bottom-width: 0;
	$bottom-color: rgba(0, 0, 0, 0);
	$left-width: 0;
	$left-color: rgba(0, 0, 0, 0);

	@if $direction == (to top) {
		$left-width: $width / 2;
		$right-width: $width / 2;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to top right) or $direction == (to right top) {
		$right-width: $width;
		$right-color: $color;
		$bottom-width: $height;
	} @else if $direction == (to right) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to bottom right) or $direction == (to right bottom) {
		$left-width: $width;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to bottom) {
		$top-width: $height;
		$top-color: $color;
		$left-width: $width / 2;
		$right-width: $width / 2;
	} @else if $direction == (to bottom left) or $direction == (to left bottom) {
		$top-width: $height;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to left) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$right-width: $width;
		$right-color: $color;
	} @else if $direction == (to top left) or $direction == (to left top) {
		$top-width: $height;
		$top-color: $color;
		$right-width: $width;
	} @else {
		$direction: false;

		@warn "Incorrect triangle direction: #{inspect($direction)}";
	}

	@if $direction {
		border-style: solid;
		border-width: $top-width $right-width $bottom-width $left-width;
		border-color: $top-color $right-color $bottom-color $left-color;
		width: 0;
		height: 0;
	}
}
@mixin visually-hidden {
	position: absolute;
	overflow: hidden;
	margin: -1px;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}
/* Slider */

.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}
.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }

    .slick-loading & {
        visibility: hidden;
    }
}
.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    [dir="rtl"] & {
        float: right;
    }
    img {
        display: block;
    }
    &.slick-loading img {
        display: none;
    }

    display: none;

    &.dragging img {
        pointer-events: none;
    }

    .slick-initialized & {
        display: block;
    }

    .slick-loading & {
        visibility: hidden;
    }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }
}
.slick-arrow.slick-hidden {
    display: none;
}
